.checkbox-legend {
	margin-left: 12px;
	cursor: pointer;
}

.checkbox-legend:hover {
	color: #2e4468;
}

.checkbox-input-div {
	margin-top: 8px;
	margin-left: 8px;
	margin-bottom: 8px;
	width: -webkit-max-content;
	width: max-content;
}

.checkbox-input {
	vertical-align: middle;
	width: 24px;
	height: 24px;
}

.number-input-span {
	float: right;
}

.number-number-input {
	width: 80px;
	margin-right: 8px;
	margin-bottom: 4px;
	padding-left: 16px;
	padding-right: 4px;
	background: rgba(186, 204, 234, 0.15);
	border: solid 1px darkgrey;
	border-radius: 2px;
	text-align: left;
	font-weight: 600;
	padding-top: 4px;
	color: rgb(92, 92, 92);
	box-shadow: inset 1px 1px 4px  rgba(186, 204, 234, 1);
}

.number-number-input:hover {
	background: rgba(186, 204, 234, 0.65);
}

.number-number-input:focus {
	background: rgba(204, 88, 0, 0.16);
	box-shadow: none;
}

.slider-outer-div {
	margin-bottom: 4px;
	margin-top: 4px;
	clear: both;
}

.slider-number-input {
	width: 80px;
	margin-right: 8px;
	margin-bottom: 4px;
	padding-left: 16px;
	padding-right: 4px;
	background: rgba(186, 204, 234, 0.15);
	border: solid 1px darkgrey;
	border-radius: 2px;
	text-align: left;
	font-weight: 600;
	font-size: 16px;
	padding-top: 4px;
	padding-bottom: 2px;
	color: rgb(92, 92, 92);
	box-shadow: inset 1px 1px 4px  rgba(186, 204, 234, 1);
}

.slider-number-input:hover {
	background: rgba(186, 204, 234, 0.65);
}

.slider-number-input:focus {
	background: rgba(204, 88, 0, 0.16);
	box-shadow: none;
}

.slider-range-input {
	width: 140px;
	margin-bottom: 4px;
	margin-left: 8px;
	margin-right: 8px;
	vertical-align: middle;
}

.slider-range-wrapper {
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	margin-left: 6px;
	margin-right: 6px;
}

.text-container-div {
	margin-bottom: 4px;
	margin-top: 4px;
	clear: both;
}

.text-inline-input {
	text-align: left;
	display: inline;
	background: rgba(186, 204, 234, 0.15);
	font-weight: 600;
	color: rgb(92, 92, 92);
	box-shadow: inset 1px 1px 4px  rgba(186, 204, 234, 1);
	border: solid 1px darkgrey;
	border-radius: 2px;
	padding-left: 3px;
	padding-right: 3px;
	padding-top: 4px;
	padding-bottom: 4px;
	min-width: 120px;
}

.text-input {
	margin-right: 8px;
	padding-left: 8px;
	padding-right: 4px;
	border: solid 1px darkgrey;
	background: rgba(186, 204, 234, 0.15);
	font-weight: 600;
	padding-top: 4px;
	padding-bottom: 4px;
	color: rgb(92, 92, 92);
	box-shadow: inset 1px 1px 4px  rgba(186, 204, 234, 1);
	min-width: 120px;
	text-align: left;
	float: right;
}

.text-inline-input:focus {
	background: rgba(204, 88, 0, 0.16);
	box-shadow: none;
}

.text-input:focus {
	background: rgba(204, 88, 0, 0.16);
	box-shadow: none;
}

.proportions-input {
	max-width: 900px;
	margin: 0 auto;
}

.proportions-number-input {
	float: left;
	width: 120px;
	text-align: center;
}

.dashboard {
	margin: 0 auto 10px;
	margin-top: 5px;
	margin-bottom: 15px;
	background: white;
	text-align: left;
}

.dashboard-button {
	margin-top: 14px;
	margin-bottom: 5px;
	top: -10px;
	max-width: 200px;
	margin-left: auto;
	margin-right: auto;
}

